    </div>
    <div class="login-box">
      <div class="login-box-title">用户登录</div>
      <div class="login-controll">
	    <div class="weui-cell">
          <input placeholder="请输入用户名（公司邮箱）" class="weui-input" v-model="user.name" v-focus></input>
		</div>
      </div>
      <div class="login-controll">
	    <div class="weui-cell">
	      <input placeholder="请输入密码（邮箱密码）" class="weui-input" v-model="user.password"></input>
		</div>
      </div>
      <div class="login-option">
        <wv-button type="primary" :disabled="option.load" :is-loading="option.load" @click.native="fnLogin()">登 录</wv-button>
      </div>
      <div style="display: none">
        <img :src="log.url"/>
      </div>
    </div>
  </div>
</template>
<script>
import services from '@/http/services'
import { Toast } from 'we-vue'
export default {
  name: 'login',
  data() {
    return {
      user:{
        name:null,
        password:null,
		userId: null,
		deviceId: null
      },
      option:{
        load: false
      },
      log:{
        url:'',
      }
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods:{
    fnLogin: function(){
      var self = this
      if(!this.user.name){
	    Toast.fail({message: '请填写用户名', duration: 2000})
        return;
      }
      if(!this.user.password){
        Toast.fail({message: '请填写密码', duration: 2000})
        return
      }
      let postData = {'user_name':this.user.name, 'password':this.user.password, 'user_id': this.user.userId, 'device_id': this.user.deviceId};
      services.httpApi.login(postData).then(function(resData){
	    Toast.success(resData.msg)
        self.$router.push('/')
      });
    },
	fnGetParam(_urls, _pname){
      let result = null;
      if(!_urls || _urls.length < 1){
        return null
      }
      if(_urls.substr(0,1) == '?'){
        _urls = _urls.substr(1)
      }
      let paramArr = _urls.split('&')
      for(let i = 0; i < paramArr.length; i++){
        if(paramArr[i].indexOf(_pname) == 0){
          let childArr = paramArr[i].split('=')
          result = childArr.length == 2 ? childArr[1] : null
          break
        }
      }
      return result
    }
  },
  created: function(){
    let urls = window.location.search.trim()
	let code = this.fnGetParam(urls, 'code')
	var self = this
	var postCodeData = {'code': code}
	Toast.loading('loading')
	services.httpApi.get_weixin(postCodeData).then(function(resData){
	  if (resData.code === 202) {
		self.user.userId = resData.data.user_id
		self.user.deviceId = resData.data.device_id
	  }
	  if (resData.code === 201) {
		self.$router.push('/')
	  }
    })
  }
}
</script>

<style scoped>
.login{height: 100%; min-height: 500px; position: relative;}
.login-logo{text-align: center; padding-top: 30px;}
.login-logo svg{width: 100px; height: 100px; color:#428bca;}
.login-box{width:80%; margin: auto; border:1px solid #428bca; border-radius: 4px; overflow: hidden; background-color: #fff;}
.login-box-title{background-color: #428bca; height: 45px; line-height: 45px; font-size:16px; color: #fff; letter-spacing: 2px;
  font-weight:bold; text-align: center; margin-bottom: 20px;}
.login-controll{border-bottom: 1px solid #ccc; margin: 10px;}
.login-option{padding: 10px;}
.page-footer{position: absolute; bottom: 0; width: 100%;}
</style>



